.project {
    margin:40px 0px;
    margin-left:60px;
    
}


.project hr{

    color:#138781;
    width: 400px;
    margin:10px auto;

}
.project h2{
    font-weight:bold;
    color: #138781;
}
#ads {
    margin:30px 0px 30px 0;
}
#ads .card-notify-badge {
    position: absolute;
    left:-10px;
    top:-20px;
    background:#f29f67;
    text-align:center;
    border-radius:30px 30px 30px 30px;
    color:black;
    padding:5px 10px;
    font-size:14px;

}

.project .card img{
    height:175px;
    width:100%;
}

.project .card-detail-badge{

    background-color:#138781;
    text-align:center;
    border-radius:30px 30px 30px 30px;
    color:black;
    padding:5px 10px;
    font-size:14px;
}
.project .card-image-overly{
    font-size:20px;
}
.project .card-image-overly span {
    display:inline-block;
    margin-left:5px;
    color:white; 

}
.project .card:hover{
    background-color:white;
    border-radius:4px;
    box-shadow:12px 15px 20px 0px rgba(46, 61, 73,0.15 );
    transition:all 1s ease;
}
.project .ad-btn{
    text-transform:uppercase;
    width:150px;
    height:40px;
    display:block;
    text-align:center;
    background-color:blue;
    color:white;
    border-radius:80px;
    font-size:16px;
    line-height:35px;
    text-decoration:none;
    margin:20px auto 20px auto;
    overflow:hidden;
    position:relative;
}
.ad-btn:hover{
    background-color:white;
    color:#138781;
    background:transparent;
    border: 2px solid blueviolet;
    transition:all 0.5s ;
}
