.navbar-profile-pic img{
    border-radius:90px;
    height:150px;
    width:150px;
    display:flex;
    vertical-align:middle;
    margin:0 auto;
    margin-top:20px;
}
.nav-items{
    color:white;
    margin-top:30px;
    margin-left:25px;
    /* text-align:center; */
}
.nav-link{

    margin-top:20px;
    cursor:pointer;
}
.nav-link svg{
    margin-right:10px;
}
.nav-link:hover{
    color:blue
}
.nav-link .active{
     color:#f29f67;
     border-left:5px solid #f29f67;
     padding-left:10px;
     text-decoration:none;
}