.techstack{
    padding-left : 130px;
    margin-top:40px 0px;

}
.techstack hr{

    color:#138781;
    width: 400px;
    margin:10px auto;

}
.techstack h2{
    font-weight:bold;
    color: #138781;
}
.techstack .card {
   cursor:pointer;
}
.techstack .tech-icon {
    font-size:2 rem;
    margin-right:10px;
}
.techstack .card:hover {
    background-color:#138781;
    color:white;
    /* border:none; */
    transition:1s;
    box-shadow:rgba(0,0,0,0.35) 0px 5px 15px;
}
