

.home-container{
   
    height:300px;
    color: white;
    padding-left:150px;
    /* margin-left:150px; */
    
}
.home-content h2{
    padding:5px;
    padding-top:80px;
    margin-left: 150px;
}
.home-content h1{
    color: #e0b50f;
    padding:5px;
    margin-left:150px;
}
.home-buttons{
    margin-left:150px;
}

.home-buttons .btn-hire{

    
    background-color: #3b8ff3;
    color:white;
    width:160px;
}

.home-buttons .btn-hire:hover{
    
    background-color:white;
    color:blue;
    width:200px;
    transition:1s;
}
.home-buttons .btn-CV{
    background-color: orange;
    color:white;
    width:160px;
    margin-left:10px;
}
.home-buttons .btn-CV:hover{
  opacity:80%;
  transition:1s;
}
.theme-btn{
    display:flex;
    align-items:center;
    justify-content:center;
    height:50px;
    width:50px;
    background-color:#1e1e1e;
    margin-top:20px;
    text-align:center;
    position:fixed;
    right:0;
    cursor:pointer;
    border-top-left-radius:10px;
    border-bottom-left-radius:10px;

    z-index:1;
}